// IMPORT MODULES
import React, { useState, useEffect, useContext } from "react";
import { LanguageContext } from "../../backend/LanguageContext";

// IMPORT IMAGES
import Next from "../../icons/arrow-circle-right.svg";

// IMPORT HELPERS
import { ValidatePassword } from "../../validators/ValidatePassword";
import { reset } from "../../backend/restore";

const Stage2 = ({ setRecoveryOpen, setRecStage, setLoader, setSuccess }) => {
  const [inputValid, setInputValid] = useState({
    newPassword: false,
    confirm: false,
    code: "",
  });
  const form = new FormData();
  const { locale } = useContext(LanguageContext);
  const [valid, setValid] = useState(false);
  const [error, setError] = useState(false);

  const [btnEnabled, setBtnEnabled] = useState(false);
  useEffect(() => {
    if (
      ValidatePassword(inputValid.newPassword ? inputValid.newPassword : "") &&
      inputValid.newPassword === inputValid.confirm &&
      inputValid.code
    ) {
      setBtnEnabled(true);
    } else {
      setBtnEnabled(false);
    }
  }, [inputValid]);

  return (
    <div className="recovery-block recovery-2">
      <form
        onSubmit={(e) => {
          e.preventDefault();
        }}
      >
        <h2 className="form-title">
          {locale === "TKM" ? "Tze aar szi" : " "}
        </h2>
        <div className="reg-input-block rec-input">
          <label htmlFor="code">
            {locale === "TKM" ? "Ijeledirme kody" : " "}
            <span>*</span>
          </label>
          <input
            autoComplete="true"
            type="text"
            id="code"
            name="code"
            required
            onChange={(e) => {
              setValid(true);
              setInputValid({ ...inputValid, code: e.target.value });
            }}
          />
          {error ? (
            <span className={"pass-check active"}>
              {locale === "TKM"
                ? "Ndogry kod girizildi"
                : "  "}
            </span>
          ) : (
            ""
          )}
        </div>
        <div className="reg-input-block rec-input">
          <label htmlFor="new-pass">
            {locale === "TKM" ? "Aar szni girizi" : " "}
            <span>*</span>
          </label>
          <input
            autoComplete="true"
            type="password"
            id="new-pass"
            name="new-pass"
            required
            onChange={(e) => {
              setValid(true);
              if (ValidatePassword(e.target.value)) {
                setInputValid({
                  ...inputValid,
                  newPassword: e.target.value,
                });
              } else {
                setInputValid({
                  ...inputValid,
                  newPassword: false,
                });
              }
            }}
          />
          {valid ? (
            <span
              className={
                inputValid.newPassword ? "pass-check" : "pass-check active"
              }
            >
              {locale === "TKM"
                ? "Aar szi  8 belgiden az bolmadyk we 15 belgiden kn bolmadyk we i azyndan 1 san, 1 ba harp we 1 setir harp, 1 rite bellikden ybarat bolmalydyr. "
                : "     1 , 1   1  , 1  ,     8    15   ."}
            </span>
          ) : (
            ""
          )}
        </div>
        <div className="reg-input-block rec-input">
          <label htmlFor="confirm">
            {locale === "TKM" ? "Aar szni gatala" : " "}
            <span>*</span>
          </label>
          <input
            autoComplete="true"
            type="password"
            id="confirm"
            name="confirm"
            required
            onChange={(e) => {
              setValid(true);
              setInputValid({ ...inputValid, confirm: e.target.value });
            }}
          />
          {valid ? (
            <span
              className={
                inputValid.newPassword === inputValid.confirm
                  ? "pass-check"
                  : "pass-check active"
              }
            >
              {locale === "TKM"
                ? "Aar szler gabat gelmeli"
                : "  "}
            </span>
          ) : (
            ""
          )}
        </div>
        <div className="rec-btn">
          <button
            disabled={!btnEnabled}
            type="button"
            className="sign-btn"
            onClick={() => {
              setLoader(true);
              form.append("password", inputValid.newPassword);
              form.append("code", inputValid.code);

              reset(form, setLoader, setError, () => {
                setSuccess(true);
                setTimeout(() => {
                  setSuccess(false);
                }, 2000);
                setRecStage(1);
                setRecoveryOpen(false);
              });
            }}
          >
            <div>
              <h3>{locale === "TKM" ? "tget" : ""}</h3>
              <div className="btn-img">
                <img src={Next} alt="next" />
              </div>
            </div>
          </button>
        </div>
      </form>
    </div>
  );
};

export default Stage2;
